.imageContainer {
  padding: 10% 0%;
  color: white;
  font-family: "Quicksand", sans-serif;
  display: grid;
  grid-template-columns: repeat(10, 10%);
  background-image: url("./whm.jpg");
  background-position: center;
  background-size: cover;
}

.imageContainer > p {
  font-size: 50px;
  text-align: right;
  grid-column: 2 / 6;
}
