#idCursos {
  padding-bottom: 5%;
}

.contenedorGrid {
  display: grid;
  overflow: hidden;
  height: auto;
  justify-content: center;
  gap: 2em;
}

.gridSliceCursos {
  grid-template-columns: repeat(4, 22%);
}

.containerProyectCursos {
  color: #330033;
  box-shadow: 5px 5px 10px var(--bgBaseMagenta);
}

.containerProyectCursos > img {
  width: 100%;
  height: 200px;
}

.containerProyectCursos > p {
  text-align: center;
}

.titleProfolio {
  text-align: center;
  color: black;
  padding: 20px;
}

.divButtonFlx {
  display: flex;
  justify-content: center;
  margin: 0% 0% 3% 0%;
}

.divButtonFlx > button {
  padding: 12px;
  color: white;
  background-color: var(--bgBaseGreenDark);
  text-transform: uppercase;
  border: none;
  border-radius: 20px;
}

@media screen and (max-width: 600px) {
  .imageContainer > p {
    font-size: 20px;
    grid-column: 1 / 6;
  }

  .divButtonFlx {
    margin: 3% 0%;
  }

  .containerProyectCursos > img {
    height: 150px;
  }

  .gridSliceCursos {
    grid-template-columns: repeat(1, 270px);
  }
}
