.footer {
  padding: 2% 0% 0% 0%;
  color: white;
}

.footerGrid {
  display: grid;
  padding: 2% 0%;
  grid-template-columns: repeat(3, 30%);
  justify-content: space-around;
}

.footerGrid > div {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.footerGrid h3 {
  font-size: 35px;
  font-weight: 100;
  letter-spacing: 2px;
}

.footerGrid img {
  width: 70%;
}

.footerGrid a {
  text-align: center;
}

.textoSubFooter {
  padding: 0.5%;
  margin-bottom: 0%;
  font-size: 13px;
  text-align: center;
}

.icons_reference {
  margin: 0% 0%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  justify-items: center;
}

.txt-left {
  text-align: left;
}

.txt-right {
  text-align: right;
}

.iconGoToTop {
  visibility: hidden;
  position: fixed;
  cursor: pointer;
  bottom: 50%;
  left: 0%;
  padding: 1.5%;
  background-color: red;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  font-size: 18px;
  color: white;
  z-index: 1000;
}

@media screen and (max-width: 600px) {
  .footerGrid {
    grid-template-columns: repeat(1, 85%);
  }

  .footerGrid h3 {
    font-size: 35px;
    /*font-weight: 100;
    letter-spacing: 2px;*/
  }

  .textoSubFooter {
    font-size: 10px;
  }

  .footerGrid p {
    font-size: 13px;
  }

  .txt-left,
  .txt-right {
    text-align: center;
  }

  .iconGoToTop {
    visibility: visible;
  }
}
