.contenedorGrid {
  display: grid;
  overflow: hidden;
  height: auto;
  justify-content: center;
  gap: 2em;
}

.gridSlicePorfolio {
  grid-template-columns: repeat(3, 30.2%);
}

.containerProyect {
  position: relative;
  cursor: crosshair;
  color: #330033;
}

.containerProyect > img {
  width: 100%;
  height: 200px;
}

.containerProyect > p {
  text-align: center;
}

.overlay {
  position: absolute;
  bottom: 0;
  background: rgb(0, 0, 0);
  background: rgba(0, 0, 0, 0.5); /* Black see-through */
  color: #f1f1f1;
  width: 100%;
  transition: 0.4s ease;
  opacity: 0;
  color: white;
  font-size: 20px;
  padding: 20px;
  text-align: center;
  margin: 0%;
}

.containerProyect:hover .overlay {
  opacity: 1;
}

.titleProfolio {
  text-transform: uppercase;
  font-family: "Quicksand", sans-serif;
  text-align: center;
  color: black;
  padding: 20px;
}

.divButtonFlx {
  display: flex;
  justify-content: center;
  margin: 2% 0%;
}

.divButtonFlx > button {
  padding: 12px;
  color: white;
  background-color: var(--bgBaseGreenDark);
  text-transform: uppercase;
  border: none;
  border-radius: 20px;
}

@media screen and (max-width: 600px) {
  .imageContainer > p {
    font-size: 20px;
    grid-column: 1 / 6;
  }

  .containerProyect > img {
    height: 150px;
  }

  .gridSlicePorfolio {
    grid-template-columns: repeat(1, 300px);
  }
}
